<template>
  <div class="equipment-view">
     <!-- <img src="../../../../assets/imgs/temp.png" alt="" style="width:200px;height:200px;"> -->
     <div class="floor" v-for="(item1, index1) in myData" :key="index1">
       <p>{{item1.floor}}&nbsp;&nbsp;平均温度  {{item1.averageTemperature}}℃</p>
       <ul>
         <!-- 展示温度 -->
         <template v-if="showTemp">
          <li v-for="(item2, index2) in item1.roomList" :key="index2" :style="`background-color: ${valueToColor(item2.temp, referList)}`">
            <span>{{item2.temp}}</span>
          </li>
         </template>
         <!-- 展示湿度 -->
         <template v-else>
          <li v-for="(item2, index2) in item1.roomList" :key="index2" :style="`background-color: ${valueToColor(item2.humidity, referList)}`">
            <span>{{item2.humidity}}</span>
          </li>
         </template>
       </ul>
     </div>
     
     <reference :referList="referList" width="300px" unitText="℃"></reference>

  </div>
</template>

<script>
// import axios from 'axios'
import reference from '../../../../../library/components/others/reference'
import valueToColor from '../../../../../library/utils/vauleToColor.js'
export default {
  components: {
    reference
  },

  mixins: [],

  props: {

  },

  data () {
    return {
      valueToColor: valueToColor,
      referList: [
