          color: '#f7c140'
        },
        {
          value: 40,
          color: '#fb6f69'
        }
      ],
      showTemp: true,

      myData: [
        {
          floor: '第一层', // 楼层
          averageTemperature: 12.5, // 平均温度
          averageHumidity: 12.5, // 平均湿度
          roomList: [
            {
              temp: -200, // 房间温度
              humidity: 25 // 房间湿度
            },
            {
              temp: 5.6, // 房间温度
              humidity: 14.4 // 房间湿度
            },
            {
              temp: 29.6, // 房间温度
              humidity: 32.4 // 房间湿度
            },
            {
              temp: 24.6, // 房间温度
              humidity: 22.4 // 房间湿度
            },
            {
              temp: 35.6, // 房间温度
              humidity: 19.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 1.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 2.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 1.4 // 房间湿度
            }
          ]

        },
        {
          floor: '第二层', // 楼层
          averageTemperature: 12.5, // 平均温度
          averageHumidity: 12.5, // 平均湿度
          roomList: [
            {
              temp: 25.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 5.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 29.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 24.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 35.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 12.4 // 房间湿度
            },
            {
              temp: 30.6, // 房间温度
              humidity: 12.4 // 房间湿度
            }
          ]

        }
      ]
    }
  },

  computed: {

  },

  mounted () {
  },

  methods: {

  }

}
</script>

<style scoped lang="less" >
.equipment-view {
  .floor  {
    ul {
      li {
        display: inline-block;
        width: 200px;
        height: 100px;
        position: relative;
        margin-right: 5px;
        span {
          position: absolute;
          padding: 3px 5px;
          background: #fff;
          right: 10px;
          bottom: 10px;
          border-radius: 3px;
        }
      }
    }
  }

  .legend {
   .line {
      width: 300px;
      height: 10px;
      background: linear-gradient(to right, #7787FE, #5cace5, #95e078, #f3e650 62.5%, #f7c140 75%, #fb6f69);
      border-radius: 3px;
   }
   ul {
     li {
       float: left;
       &:not(:first-child) {
         padding-left: 50px;
       }
       &:nth-child(4), &:nth-child(5) {
         padding-left: 33px;
       }
     }
   }
  }
}
</style>



